import React, { useEffect, useRef, useState } from 'react';
import {Modal, Button, Input, DatePicker, Checkbox, Select} from 'antd';
import { ethers } from "ethers"
import { useWeb3 } from '../../api/contracts';
import api from "../../api";
import left from '../../img/left.png';
import right from '../../img/right.png';

import NewVoteTop from './newVoteTop';
import { useTranslation } from "react-i18next";
import moment from 'moment';
import styled from 'styled-components';
import { getShortDisplay } from '../../utils/publicJs';

import ManageFeeTransfer from './newVote/manageFee'
import ApproveGP from './newVote/approveGP';
import KickGP from './newVote/kickGP'
import AddGP from './newVote/addGP'

import TokenSelect from './newVote/tokenSelect'


const AddButton = styled(Button)`
    width: 100%;
    height: 4.4rem;
    background: #F1F3FA;
    box-shadow: 0rem 0rem 0.4rem 0rem rgba(16, 22, 75, 0.1);
    border-radius: 0.8rem;
    font-size: 1.6rem;
    color: #10164B !important;
    line-height: 1.9rem;
`

