export default function VentureConfig(props) {

    const { state, dispatch } = useWeb3();
    const { maincontract, daoManagercontract, web3Instance, Account } = state;

    const [addressList, setAddressList] = useState([{
        address: '',
        bonus: null
    }])
    const [start, setstart] = useState(false);

    const [logo, setlogo] = useState('');
    const [daoname, setdaoname] = useState('');
    const [desc, setdesc] = useState('');

    const [next, setnext] = useState(false);
    const [adminstate, setadminstate] = useState(false);
    const [errorShow, seterrorShow] = useState(false);
    const [errorTips, seterrorTips] = useState('');

    const [componentsList, setComponentsList] = useState(null)
    const [newDaoId, setNewDaoId] = useState(null);
    const [newDaoBase, setNewDaoBase] = useState({})

    const [configData, setConfigData] = useState({})
    const [selectToken, setSelectToken] = useState({})

    const [socialUrls, setSocialUrls] = useState('')


    let { t } = useTranslation();
    const voteRef = useRef();

    const saveStepData = ()=>{
        sessionStorage.setItem('forthStep', JSON.stringify({
            template: 1,
            data: {
                configData, 
            }
        }));
    }

    const checkBonus = ()=>{
        const bonus_arr = addressList.map(a=>ethers.BigNumber.from(a.bonus || 0))
        const v = bonus_arr.reduce(function(accumulator,currentValue,currentIndex,array){
            return accumulator.add(currentValue);
        });
        return v.toNumber() === 100
    }

    const checkInvalidAddress = useCallback((addressList)=>{
        for (const a of addressList) {
            if (!ethers.utils.isAddress(a.address)) return a.address
        }
    },[])

    const Submit = () => {
        if (!configData.tokenName || !configData.tokenSymbol || !configData.GpCarry || !configData.manageFee || !configData.softCap || !configData.hardCap) {
            dispatch({ type: 'MSGTYPE', payload: { msg: t('create.venture.FillTip'), closable: true } });
            return
        }
        
        const invalidAddress = checkInvalidAddress(addressList)
        if (invalidAddress) {
            dispatch({ type: 'MSGTYPE', payload: { msg: t('create.GpAddressInvalid', {address: invalidAddress}), closable: true } });
            return
        }
        if (!checkBonus()) {
            dispatch({ type: 'MSGTYPE', payload: { msg: t('create.BonusMatch'), closable: true } });
            return
        }
        if (voteRef.current.checkInvalid()) {
            dispatch({ type: 'MSGTYPE', payload: { msg: t('vote.VoteConfigError'), closable: true } });
            return
        }
        saveStepData()
        toDataFormat()
    }

    const toThirdStep = () => {
        props.handlerSet(2);
        saveStepData()
    }
   

    const handleInput = (e) => {
        const { name, value } = e.target;
        const newConfigData = {
            ...configData,
            [name]: value
        }
        setConfigData(newConfigData)
    }
    const handleNumberInput = (name, value) => {
        const newConfigData = {
            ...configData,
            [name]: value
        }
        setConfigData(newConfigData)
    }

    useEffect(() => {
        try {
            let form = JSON.parse(sessionStorage.getItem('forthStep'));
            if (form && form.template === 1) {
                setConfigData(form.data.configData)
            }
            
        } catch (error) {
            
        }

        fundList.length > 0 && setSelectToken(fundList[0])
    }, []);


    const toDataFormat = () => {
        const firstStep = JSON.parse(sessionStorage.getItem('firstStep'));
        // const imgUrl = JSON.parse(sessionStorage.getItem('imgUrl'));

        if (firstStep != null) {
            setdaoname(firstStep.name);
            setdesc(firstStep.description);
            setlogo(firstStep.imgUrl);
            setSocialUrls(firstStep.socialUrls || '');
        }

        const secondStep = JSON.parse(sessionStorage.getItem('secondStep'));
        setComponentsList(secondStep)

        // const thirdStep = JSON.parse(sessionStorage.getItem('thirdStep'));
        // const {admin,adminlist} = thirdStep;

        setadminstate(false);
        // if(admin){
        //     setadminlist(adminlist);
        // }

        setstart(true)
    }




    useEffect(() => {
        if (!start) return;
        const initDao = async ()=>{
            dispatch({ type: 'LOADINGTYPE', payload: t('create.InstanceByTemplate') });
            console.log('VenturesManager: ', configData.GpCarry, configData.manageFee)
            console.log('** addressList: ', addressList.map(a=>a.address), addressList.map(a=>Number(a.bonus)))

            try {
                const signer = web3Instance.getSigner(Account)
                const res = await maincontract.connect(signer).instanceByTemplate(
                    [daoname, logo, desc], 
                    socialUrls,
                    "VentureTemplate", 
                    [
                        "AuthManager", 
                        "VaultManager",
                        "ManageFeeVaultManager",
                        "VenturesStockManager",
                    ],
                    [
                        // AuthManager
                        "0x", 
                        // VaultManager
                        ethers.utils.defaultAbiCoder.encode(
                            ['address'],
                            [selectToken.address]
                        ),
                        // ManageFeeVaultManager
                        ethers.utils.defaultAbiCoder.encode(
                            ['address'],
                            [selectToken.address]
                        ),
                        // VenturesStockManager
                        ethers.utils.defaultAbiCoder.encode(
                            ['string', 'string'],
                            [configData.tokenName, configData.tokenSymbol]
                        ),
                    ]
                );
                const r = await res.wait();
                const event = r.events.filter(e=>e.event === 'InstanceDAO')[0]
    
                const newDaoId = event.args[0].toString()
                setNewDaoId(newDaoId)
            } catch (error) {
                console.error(error)
                seterrorShow(true)
                seterrorTips(`init dao failed ${(error && error.data && error.data.message) || ''}`)
                dispatch({ type: 'LOADINGTYPE', payload: null });
                setstart(false);
            }
        }
        initDao()
        
    }, [maincontract, start]);

    useEffect(async()=>{
        if (!newDaoId) return
        const getDao = async()=>{
            try {
                const data = await maincontract.instanceMap(parseInt(newDaoId));
                setNewDaoBase(data)
            } catch (error) {
                console.error('instanceMap failed', error)
            }
        }
        getDao()
    }, [newDaoId])


    useEffect(()=>{
        if (Object.keys(newDaoBase).length === 0) return
        const getVenturesManagerParams = (vsm)=>{
            const p1 = [
                ethers.BigNumber.from(configData.GpCarry*100), 
                ethers.BigNumber.from(configData.manageFee*100), 
                ethers.utils.parseUnits(String(configData.softCap), selectToken.deci),
                ethers.utils.parseUnits(String(configData.hardCap), selectToken.deci),
                selectToken.address,
                Account,
                vsm
            ]
            const p2 = [
                addressList.map(a=>a.address), 
                addressList.map(a=>ethers.BigNumber.from(a.bonus*100))
            ]
            return ethers.utils.defaultAbiCoder.encode(
                ['bytes', 'bytes'],
                [
                    ethers.utils.defaultAbiCoder.encode(
                        ['uint256', 'uint256', 'uint256', 'uint256', 'address', 'address', 'address'],
                        p1,
                    ),
                    ethers.utils.defaultAbiCoder.encode(
                        ['address[]', 'uint256[]'],
                        p2
                    )
                ]
                
            )
        }

        const getOrgManagerParams = ()=>{
            return ethers.utils.defaultAbiCoder.encode(
                // uint256 memberType, string[] memory names, address[] memory accounts
                ['uint256', 'string[]', 'address[]'],
                [ 2, addressList.map(m=>""), addressList.map(a=>a.address) ]
            )
        }
        const getVoteManagerParams = ()=>{
            const { minNumber, minSupportNumber } = voteRef.current.getVoteConfig()
            return ethers.utils.defaultAbiCoder.encode(
                ['uint256', 'uint256'],
                [minNumber, minSupportNumber]
            )
        }
        const getGrantMethodManagerParams = ()=>{
            return "0x"
        }
        const getVoteExecutionManagerParams = ()=>{
            return "0x"
        }

        const addComponents = async()=>{
            dispatch({ type: 'LOADINGTYPE', payload: `${t('venture.manager')}` });
            let vsm;
            try {
                const daoOrg = await api.daoOrg.getContract(web3Instance, newDaoBase.daoOrganization) 
                const templateConfigAddress = await daoOrg.templateConfig()
                const templateConfig = await api.template.getContract(web3Instance, templateConfigAddress)
                vsm = await templateConfig.labelComponent('VenturesStockManager')
            } catch (error) {
                console.error('get VenturesStockManager address failed', error)
            }
            try {
                const signer = web3Instance.getSigner(Account)
                const res = await maincontract.connect(signer).addComponents(newDaoId, [
                    'VenturesManager',
                    'OrgManager',
                    'VoteManager',
                    'GrantMethodManager',
                    'VoteExecutionManager'
                ], [
                    getVenturesManagerParams(vsm),
                    getOrgManagerParams(),
                    getVoteManagerParams(),
                    getGrantMethodManagerParams(),
                    getVoteExecutionManagerParams(),
                ])
                await res.wait()
                setnext(true);
            } catch (error) {
                showError(t('venture.manager'), error)
            } finally {
                dispatch({ type: 'LOADINGTYPE', payload: null });
            }
        }
        addComponents()
    }, [newDaoBase])

    const showError = (preMsg, error)=>{
        console.error(error)
        seterrorShow(true)
        seterrorTips(`${preMsg}: ${error.data && error.data.data && error.data.data.message}`)
        dispatch({ type: 'LOADINGTYPE', payload: null });
    }

    useEffect(()=>{
        if (!next) return
        sessionStorage.removeItem("step");
        sessionStorage.removeItem("secondStep");
        sessionStorage.removeItem("firstStep");
        sessionStorage.removeItem("ImageUrl");
        sessionStorage.removeItem("forthStep");
        dispatch({ type: 'LOADINGTYPE', payload: null });
        props.history.push(`/about/${newDaoId}`);
        dispatch({ type: 'RELOAD_DAOS', payload: true });
    }, [next])

    const addAddress = ()=>{
        let newArray = [...addressList];
        newArray.push({
            address: '',
            bonus: ''

        });
        setAddressList(newArray)
    }

    const handleSetGpBonus = (name, value, index) => {
        let newArray = [...addressList];
        newArray[index][name] = value;
        setAddressList(newArray)
    }

    const deleteAddress = (e, index)=>{
        let newArray = [...addressList];
        newArray.splice(index, 1);
        setAddressList(newArray)
    }

    const handleSelectToken = (value) => {
        setSelectToken(fundList[value])
    }

    const getValidGpLength = useMemo(() => {
        let i = 0
        addressList.forEach(a=>a.address && i++)
        return i
    }, [addressList])




    return <Content className="content">

        <Modal
            visible={errorShow}
            onCancel={() => seterrorShow(false)}
            footer={null}
        >
            <Tip>{errorTips}</Tip>
        </Modal>
        <Info>
            <div className="title">{t('create.TemplateConfiguration')}</div>
        </Info>

        <Translation>{t =>
            <div title={t('Token')} >
                <div className="line">
                    <div className='title'>{t('create.venture.info')}</div>
                    <div className='line-block'>
                        <div className='block'>
                            <div className="subtitle font-light">{t('create.venture.GP')}</div>
                            <div className='inputBrdr'>
                                <InputNumber 
                                    placeholder={t('create.venture.GPTips')}
                                    controls={false}
                                    precision={2}
                                    max={100}
                                    onChange={(v)=>handleNumberInput('GpCarry', v)}
                                />
                                <span className='suffix'>%</span>
                            </div>
                        </div>
                        <div className='block'>
                            <div className="subtitle font-light">{t('create.venture.Manage')}</div>
                            <div className='inputBrdr'>
                                <InputNumber 
                                    placeholder={t('create.venture.GPTips')}
                                    controls={false}
                                    precision={2}
                                    max={100}
                                    onChange={(v)=>handleNumberInput('manageFee', v)}
                                />
                                <span className='suffix'>%</span>
                            </div>
                        </div>
                    </div>
                    
                </div>

                <div className="line line2">
                    <div className='title'>{t('create.venture.GPinfo')}</div>
                    <div className="subtitle font-light">{t('create.venture.address')}</div>
                    {
                        addressList.map((item, idx) => {return (
                            <div className='line-block address-item' key={idx}>
                                <div className='inputBrdr address-block'>
                                    <Input
                                        placeholder={t('create.enter')}
                                        value={addressList[idx].address}
                                        autoComplete="off"
                                        onChange={(e) => handleSetGpBonus('address', e.target.value, idx)} />
                                </div>
                                <div className='inputBrdr bonus-block'>
                                    <InputNumber 
                                        placeholder={t('create.venture.Bonus')}
                                        value={addressList[idx].bonus}
                                        controls={false}
                                        precision={2}
                                        max={100}
                                        onChange={(v)=>handleSetGpBonus('bonus', v, idx)}
                                    />
                                    <span className='suffix'>%</span>
                                </div>
                                {!!idx && <div><img src={remove} alt="" onClick={(e) => deleteAddress(e, idx)} /></div>}
                            </div>
                        )})
                        
                    }
                    <button className='btn-add font-light' onClick={addAddress}>{t('create.venture.AddMore')}</button>


                </div>

                <div className="line line3">
                    <div className='title'>{t('create.venture.Found')}</div>
                    <div className='line-block'>
                        <div className='block'>
                            <div className="subtitle font-light">{t('create.venture.TokenType')}</div>
                            <SelectBrdr style={{ width: '100%' }} onChange={handleSelectToken} placeholder={t('create.venture.SelectOption')}>
                                {
                                    fundList.map((item, i) =>
                                        <Select.Option value={i} key={item.address}>
                                            <div style={{ display:'flex', alignItems:'center', height: '100%', lineHeight: '4.4rem'}}>
                                                <div style={{ display:'flex', flexDirection:'row', flexGrow:1}}>
                                                    <div style={{lineHeight: '3.55rem', fontWeight: 400, textTransform:'uppercase', marginRight: '0.5rem'}}>{item.symbol}</div>
                                                    <div style={{color: '#A6A6B7', lineHeight: '3.55rem'}}>{`${item.address.slice(0,6)}…${item.address.slice(-6)}`}</div>
                                                </div>
                                            </div>
                                        </Select.Option>
                                    )
                                }
                            </SelectBrdr>
                            
                        </div>
                        <div className='block'>
                            <div className="subtitle font-light">{t('create.venture.softcap')}</div>
                            <div className='inputBrdr'>
                                <InputNumber 
                                    placeholder={t('create.enter')}
                                    controls={false}
                                    onChange={(v)=>handleNumberInput('softCap', v)}
                                />
                                    <span className='suffix'>{selectToken.symbol}</span>
                            </div>
                        </div>
                        <div className='block'>
                            <div className="subtitle font-light">{t('create.venture.hardcap')}</div>
                            <div className='inputBrdr'>
                                <InputNumber 
                                    placeholder={t('create.enter')}
                                    controls={false}
                                    onChange={(v)=>handleNumberInput('hardCap', v)}
                                />
                                    <span className='suffix'>{selectToken.symbol}</span>
                            </div>
                        </div>
                        
                    </div>
                    <div className='line-block'>
                        <div className='block'>
                            <div className="subtitle font-light">{t('create.venture.TokenName')}</div>
                            <div className='inputBrdr'>
                                <Input
                                    placeholder={t('create.venture.FillTokenName')}
                                    name='tokenName'
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                        <div className='block'>
                            <div className="subtitle font-light">{t('create.venture.TokenSymbol')}</div>
                            <div className='inputBrdr'>
                                <Input
                                    placeholder={t('create.venture.FillTokenSymbol')}
                                    name='tokenSymbol'
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                    </div>

                    <VentureVoteConfig ref={voteRef} gp={getValidGpLength} titleClass='title' />
                    
                </div>
                
                <div className='tip'>
                    <div className='img-box'><img src={WranImg} alt="" /></div>
                    <div className='tip-text'>{t('create.warningTips')}</div>
                </div>
               
            </div>

        }
        </Translation>

        <div className="line" style={{ textAlign: 'right', marginTop: '4rem' }}>
            <Button className='previous' style={{ marginRight: '3rem' }} onClick={()=>toThirdStep()}>
                <img className="left" src={left} alt="" />
                {t('create.think')}
            </Button>
            {/* <Button type="primary" onClick={()=>Submit()} className="nextBtn"  disabled={!showDisable}> */}
            <Button type="primary" onClick={()=>Submit()} className="nextBtn">
                 {t('create.submit')}
                <img src={right} alt="" />
            </Button>
        </div>

    </Content>

}
