export default function FirstStep(props) {
    const { state, dispatch } = useWeb3();
    const { web3Instance, maincontract, Account } = state
    let { t } = useTranslation();

    const [imgUrl, setimgUrl] = useState('');
    const [errTips, seterrTips] = useState(false);
    const [showDisable, setshowDisable] = useState(true);
    const [name, setName] = useState('');
    const [twitter, setTwitter] = useState('');
    const [description, setdescription] = useState('');

    const logoRef = useRef()

    const handleChange = (e) => {
        const { name, value } = e.target;

        if (name === "name") {
            setName(value)
        } else if (name === "twitter") {
            setTwitter(value)
        } else {
            setdescription(value)
        }
    }

    useEffect(() => {
        const form1 = JSON.parse(sessionStorage.getItem('firstStep'));
        // const imgUrl1 = JSON.parse(sessionStorage.getItem('imgUrl'));
        if (form1 != null) {
            setName(form1.name);
            setdescription(form1.description);
            setimgUrl(form1.imgUrl);
            if (form1.socialUrls) {
                const urls = form1.socialUrls.split(';')
                setTwitter(urls[0])
            }
            setshowDisable(form1.imgUrl && form1.name.length && form1.description.length)
        }

    }, []);

    useEffect(() => {
        if (twitter && !twitter.startsWith('https://twitter.com/')) {
            setshowDisable(false)
            return
        }
        setshowDisable(imgUrl && name.length && description.length)

    }, [imgUrl, name, description, twitter]);

    const toSecondStep = async() => {
        const data = {
            name,
            description,
            imgUrl
        }
        if (!!twitter) data.socialUrls = twitter
        sessionStorage.setItem('firstStep', JSON.stringify(data))
        // sessionStorage.setItem('imgUrl', JSON.stringify(imgUrl))

        if (data.imgUrl && name && description) {
            props.handlerSet(2);

            // const baseInfo = [name, imgUrl, description];
            // const signer = web3Instance.getSigner(Account)
            // // TODO loading
            // const res = await maincontract.connect(signer).instanceByTemplate(baseInfo, "DaoTemplate", []);
            // console.log('*** res:', res)
            // if (res) {
            //     sessionStorage.setItem('firstStep', JSON.stringify({
            //         id: '1'
            //     }))
            //     props.handlerSet(2);
            // } else {
            //     seterrTips(true)
            // }
        } else {
            seterrTips(true)
        }
    }
    const handleImageChange = (e) => {
        setimgUrl(e.target.value)
    }

    const removeImage = () => {
        setimgUrl('');
        sessionStorage.setItem('ImageUrl', '')
    }


    return <div className="content">
        <Modal
            visible={errTips}
            onCancel={() => seterrTips(false)}
            className='newVoteBrdr homebtm'
            footer={null}
        >
            <Tip>
                {t('create.FillTips')}
            </Tip>
        </Modal>
        <Info>
            <div className="title">{t('create.Basicinformation')}</div>
            {/* <div className="detail mt1">
                DAOWallet Will Soon Be Launched On Chrome Web Store. It Will Gradually Support Most Types .
            </div> */}
        </Info>
        <Content>
            {/* {
                imgUrl && <div className='uploadBrdr'>
                    <div className='title'>{t('create.DAOImage')}</div>
                    <div className='imgBrdr'>
                        <img src={imgUrl} alt="" />
                        {
                            <img src={closeBtn} className='removeBrdr' onClick={removeImage} alt='' />
                        }
                    </div>
                </div>
            } */}
            {
                <div className='uploadBrdr'>
                    <div className='title'>{t('create.DAOImage')}</div>
                    <LogoUpload ref={logoRef} imgCode={imgUrl} updateUrl={url=>setimgUrl(url)}/>
                </div>
            }
            {/* {
                !imgUrl && <div className='uploadBrdr'>
                    <div className='title'>{t('create.DAOImage')}</div>
                    <div className="imageBox">
                        <div className="img">{t('create.ImageScale')}</div>
                        <div className="inputBrdr">
                            <Input type="text"
                                placeholder={t('create.FillImage')}
                                onChange={handleImageChange}
                            />
                        </div>
                    </div>
                </div>
            } */}

            <div className="line">
                <div className="title">{t('create.DAOName')}</div>
                <div className='inputBrdr'>
                        <Input
                            placeholder={t('create.FillName')}
                            value={name}
                            name='name'
                            autoComplete="off"
                            maxLength="60"
                            onChange={handleChange}
                        />

                </div>
            </div>
            <div className="line">
                <div className="title">{t('create.twitter')}</div>
                <div className='inputBrdr'>
                    <Input
                        placeholder={t('create.twitterTips')}
                        value={twitter}
                        name='twitter'
                        autoComplete="off"
                        onChange={handleChange}
                    />
                </div>
            </div>

            <div className="line">
                <div className="title">Description</div>
                <div className='inputBrdr textareaB'>
                        <Input.TextArea
                            placeholder={t('create.FillDAODescription')}
                            value={description}
                            name='description'
                            autoComplete="off"
                            onChange={handleChange}
                            allowClear={true}
                        />
                </div>
            </div>

            <div className="line" style={{ textAlign: 'right' }}>
                <Button className="nextBtn" type="primary" disabled={!showDisable} onClick={toSecondStep}>
                    {t('create.Next')}
                    <img src={right} alt="" />
                </Button>
            </div>
        </Content>
    </div>

}
